	methods: {
		openinfo(e) {
			console.log(e)
			var id = e.currentTarget.dataset.id
			// console.log(id)
			uni.navigateTo({
				// 传递 id 在详情页接收
				url: './userDetail/userDetail?id=' + id
			});
		},
		
		loadData(collback) {
			if (this.lodingLock) {
				return;
			}
			this.lodingLock = true;
			this.status = "loading"
			let that = this;
			let param = {
				page: that.nextPage,
				keyword: that.keyword,
				type: that.infoType,
			};
			http.apiRequest({
				api: "/api/member/memberList",
				data: param,
				secuss: (res, httpStatus) => {
					that.lodingLock = false;
					if (res.code == "1") {
						that.nextPage = res.data.nextPage;
						that.page = res.data.page;
						that.itemList = that.itemList.concat(res.data.rows);
						// console.log(that.itemList)
		
						if (that.nextPage > 0) {
							that.status = "more";
						} else {
							that.status = 'nomore';
						}
		
						if (typeof(collback) == "function") {
							collback();
						}
					} else {
						if (that.nextPage > 0) {
							that.status = "more"
						} else {
							that.status = "nomore"
						}
						uni.showToast({
							title: res.msg,
							duration: 1500,
							icon: "none"
						});
						if (typeof(collback) == "function") {
							collback();
						}
					}
				},
				error: (res, httpStatus) => {
					//接口调用失败，可能是网络故障，可能是接口报错
					if (that.nextPage > 0) {
						that.status = "more"
					} else {
						that.status = "nomore"
					}
					that.lodingLock = false;
					uni.showToast({
						title: "接口调用失败，请检查网络",
						duration: 1500,
						icon: "none"
					});
					if (typeof(collback) == "function") {
						collback();
					}
				},
			});
		},
	
